@import '_constants';

#animation-container {
    #before-river-svg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
    }
    #before-river-ui {
        position: absolute;
        top: 1%;
        bottom: 1%;
        left: 1%;
        right: 60%;
        display: flex;
        flex-direction: column;
        justify-content: start;
    }
    #before-river-next-stage {
        position: absolute;
        bottom: 1%;
        right: 1%;
    }
}
